
import React from 'react';
import './Home.css';
import image from '../../images/chicago.png';
import placeholder from '../../images/placeholder.png';

function Home() {
  return (
    <div className="home">
      <img src={image} alt="Imge of NCC"></img>

      <div className="home_projects">
        <h1>PROJECTS</h1>
        
        <div className="home_projects_1">
          <h1>C++ Project</h1>

          <div className="home_projects_1_image">
            <img src={placeholder} alt="hacker"></img>
            <button>View Project in GitHub</button>
          </div>

          <div className="home_projects_1_info">
            <div className="home_projects_1_description">
              <h3>DESCRIPTION</h3>
              <p>This is a description of the project that I created.</p>
            </div>

            <div className="home_projects_1_difficulties">
              <h3>DIFFICULTIES FACED</h3>
              <p>This is a description of the difficulties faced while creating the project.</p>
            </div>

            <div className ="home_projects_1_solutions">
              <h3>HOW I SOLVED THE DIFFICULTIES</h3>
              <p>This is how I solved the difficulties I faced while creating this project.</p>
            </div>

            <div className="home_projects_1_technology">
              <h3>TECHNOLOGIES USED</h3>

              <ul>
                <li>MongoDB</li>
                <li>Node.JS</li>
                <li>Express</li>
                <li>Java</li>
                <li>C++</li>
              </ul>
            </div>
          </div>
        </div>

        <div className="home_projects_2">
          <h1>JavaScript Web API Project</h1>

          <div className="home_projects_2_image">
            <img src={placeholder} alt="hacker"></img>
            <button>View Project in GitHub</button>
          </div>

          <div className="home_projects_2_info">
            <div className="home_projects_2_description">
              <h3>DESCRIPTION</h3>
              <p>This is a description of the project that I created.</p>
            </div>

            <div className="home_projects_2_difficulties">
              <h3>DIFFICULTIES FACED</h3>
              <p>This is a description of the difficulties faced while creating the project.</p>
            </div>

            <div className ="home_projects_2_solutions">
              <h3>HOW I SOLVED THE DIFFICULTIES</h3>
              <p>This is how I solved the difficulties I faced while creating this project.</p>
            </div>

            <div className="home_projects_2_technology">
              <h3>TECHNOLOGIES USED</h3>
              
              <ul>
                <li>MongoDB</li>
                <li>Node.JS</li>
                <li>Express</li>
                <li>Java</li>
                <li>C++</li>
              </ul>
            </div>
          </div>
        </div>

      </div>
    </div>
  );
}

export default Home;