
import React from 'react';
import './About.css';
import education_image from '../../images/test_image.png';
import athletic_image from '../../images/testTrophy.JPG';
import hobby_image from '../../images/testCoco.png';

function About() {
  return (
    <div className="about">
      <div className="about_header">
        <h1>About Me</h1>
      </div>

      <div className="about_info_education">
        <div className="about_info_header_education">
          <h1>EDUCATION</h1>
        </div>

        <div className="about_info_image_education">
          <img src={education_image} alt=""></img>
        </div>

        <div className="about_info_text_education">
          <p>I graduated from North Central College in Naperville, IL in May of 2020 with a B.S. in Computer Science. While I attended North Central, I was on an Honors Scholarship and achieved multiple Dean's List awards.</p>
        </div>
      </div>

      <div className="about_info_athletics">
        <div className="about_info_header_athletics">
          <h1>ATHLETICS and LEADERSHIP</h1>
        </div>

        <div className="about_info_image_athletics">
          <img src={athletic_image} alt=""></img>
        </div>

        <div className="about_info_text_athletics">
          <p>I was on the track and field team all four years and was a team captain my Junior and Senior seasons. While on the team, we achieved two national championship titles during the 2017 and 2019 Indoor seasons. I also obtained an All-American award for the 4x400m relay by placing 7th in the nation.</p>
        </div>
      </div>

      <div className="about_info_personal">
        <div className="about_info_header_personal">
          <h1>HOBBIES and INTERESTS</h1>
        </div>

        <div className="about_info_image_personal">
          <img src={hobby_image} alt=""></img>
        </div>

        <div className="about_info_text_personal">
          <p>In my free time, I love being outdoors with friends and doing activities like running or playing basketball. I also am constantly working on one of my many hobbies such as driving and fixing up cars, building PCs, and learning how to make my own games.</p>
        </div>
      </div>
    </div>
  );
}

export default About;
